@import "layout";

// color, font-size, font-weight


$card-icon-height: 32px;
$card-header-icon-width: 24px;

////////////////////////////////////////////////////////////////
// FIXES
////////////////////////////////////////////////////////////////

svg {
    backface-visibility: hidden;
    transform: translateZ(0);
    -webkit-font-smoothing: subpixel-antialiased;    
}

////////////////////////////////////////////////////////////////
// FONT/COLOR
////////////////////////////////////////////////////////////////

html,
body {
    font-size: 18px;
    color: #444;
}

.buttons-stores {
    flex-direction: row;
    justify-content: flex-end;
}

@media (max-width: 800px) {

    html,
    body {
        font-size: 16px;
    }

    .buttons-stores {
        flex-direction: column;
        align-items: center;
    }
}

@media (min-width: 800px) and (max-width: 1700px) {

    html,
    body {
        font-size: 20px;
    }
}

@media (min-width: 1700px) {

    html,
    body {
        font-size: 28px;
    }
}

a {
    font-weight: 600;
}


.site-titles {
    h1.site-title1 {
        font-size: 4rem;
        font-weight: 400;
    }

    h1.site-title1 .strong-text {
        font-size: 4rem;
        font-weight: 600;
        color: #000;
    }

    .site-titles2 h2.site-title2 {
        font-size: 1rem;
        font-weight: 300;
        line-height: 140%;
    }

    .site-titles2 h2.site-title2 .strong-text {
        font-size: 1rem;
        font-weight: 600;
        color: #222;
    }
}

.buttons-stores .button-store {
    font-size: 14px;
    color: #fff;
    background: rgb(0, 102, 204);
}

// .buttons-stores .button-store { font-size: 14px; color: rgb(0,125,255); background: #fff; }
// .buttons-stores .button-store { font-size: 14px; color: #fff; background: #444; }

.section {

    div { position: relative; }

    .section-header {
        .section-title1 {
            font-size: 2rem;
            line-height: 170%;
            font-weight: 500;
        }
    
        .section-title2, .section-title2 div {
            font-size: 1rem;
            line-height: 170%;
            font-weight: 300;
        }
    
        .section-title2 li {
            text-align: justify;
            font-size: 1rem;
            line-height: 170%;
            font-weight: 300;
        }    
    }
}

.section.pixodesk-section {
    .section-header .section-title1 { font-size: 2.5rem; }
    .small-card {
        // background: #fff;
        // border: 1px solid rgba(0, 0, 0, 0.1);
        // box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
    }
}


.small-card-header {
    height: 2.5em;
    margin-bottom: 0.5em;

    .small-card-title {
        font-size: 1.25rem;
        font-weight: 700;
        line-height: 100%;
    
        .text-title {
            margin-right: 1em;
            line-height: 130%;
        }    
    }    
}

.small-card-text {
    font-size: 0.8rem;
    font-weight: 300;
}

.small-card-text li {
    text-align: justify;
    font-size: 0.8rem;
    line-height: 170%;
}

.small-cards-grid {
    position: relative;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    column-gap: 1em;
    row-gap: 1em;

    .small-card {
        justify-self: center;
        max-width: 25em;
    }
}    
@media (min-width: 800px) {
    .small-cards-grid {
        grid-template-columns: 1fr 1fr;
        .small-card {
            justify-self: stretch;
        }
    }
}
@media (min-width: 1200px) {
    .small-cards-grid {
        grid-template-columns: 1fr 1fr 1fr;
        .small-card {
            justify-self: stretch;
        }
    }
}

