////////////////////////////////////////////////////////////////
// SECTIONS
////////////////////////////////////////////////////////////////

.section {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 4em;

    &.pixodesk-section {
        .small-cards-grid {
            margin-left: 5%;
            margin-right: 5%;
            max-width: 70em;    
        }
    }
    &.lottie-section {
        padding: 32px 32px 16px 32px;
        // background: #f5f5f7;
        background: #f8f8f8;
        border-radius: 16px;
        // box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
        border: 1px solid rgba(0,0,0,0.1);
        margin-left: 5%;
        margin-right: 5%;
        max-width: 70em;
    
        .small-card {
            border: none;
            background: none;
        }
    }

    .section-header {
        max-width: 60em;

        .section-title1 {
            position: relative;
            text-align: center;
        }

        .section-title2 {
            max-width: 40em;
            margin-top: 1em;
            margin-bottom: 2em;
        }
    }

    &.icons-section,
    &.splash-section {
        padding: 32px 32px 32px 32px;    
        video {
            padding: 42px;
            border-radius: 16px;
            background: #fff;
            border: 1px solid rgba(0,0,0,0.1);
            // box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
        }
    }
        
    &.icons-section {
        // background: #f5f5f7;
        background: linear-gradient(90deg, hsl(210, 0%, 98%) 0%, hsl(210, 0%, 90%) 50%, hsl(210, 0%, 98%) 100%);
    }
    
    &.loaders-section {
        video {
            padding: 42px;
            border-radius: 16px;
            background: #fff;
            // border: 1px solid rgba(0, 0, 0, 0.1);
            // box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
        }
    }    

    &.splash-section {
        // background: #f5f5f7;
        background: linear-gradient(90deg, hsl(210, 0%, 98%) 0%, hsl(210, 0%, 90%) 50%, hsl(210, 0%, 98%) 100%);
    }    
}




.icons .section-title1,
.icons .title-secondary,
.loaders .section-title1,
.loaders .title-secondary,
.splash .section-title1,
.splash .title-secondary {
    text-align: center;
}


