}



.shadow-2dp {
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
}

.shadow-3dp {
    box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.14), 0 1px 8px 0 rgba(0, 0, 0, 0.12), 0 3px 3px -2px rgba(0, 0, 0, 0.4);
}

.shadow-4dp {
    box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12), 0 2px 4px -1px rgba(0, 0, 0, 0.4);
}

.shadow-6dp {
    box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.14), 0 1px 18px 0 rgba(0, 0, 0, 0.12), 0 3px 5px -1px rgba(0, 0, 0, 0.4);
}

.shadow-8dp {
    box-shadow: 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12), 0 5px 5px -3px rgba(0, 0, 0, 0.4);
}

.shadow-12dp {
    box-shadow: 0 12px 16px 1px rgba(0, 0, 0, 0.14), 0 4px 22px 3px rgba(0, 0, 0, 0.12), 0 6px 7px -4px rgba(0, 0, 0, 0.4);
}

.shadow-16dp {
    box-shadow: 0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.4);
}

.inset-shadow-2dp {
    box-shadow: inset 0 2px 2px 0 rgba(0, 0, 0, 0.14), inset 0 1px 5px 0 rgba(0, 0, 0, 0.12), inset 0 3px 1px -2px rgba(0, 0, 0, 0.2);
}

.inset-shadow-3dp {
    box-shadow: inset 0 3px 4px 0 rgba(0, 0, 0, 0.14), inset 0 1px 8px 0 rgba(0, 0, 0, 0.12), inset 0 3px 3px -2px rgba(0, 0, 0, 0.4);
}

.inset-shadow-4dp {
    box-shadow: inset 0 4px 5px 0 rgba(0, 0, 0, 0.14), inset 0 1px 10px 0 rgba(0, 0, 0, 0.12), inset 0 2px 4px -1px rgba(0, 0, 0, 0.4);
}

.inset-shadow-6dp {
    box-shadow: inset 0 6px 10px 0 rgba(0, 0, 0, 0.14), inset 0 1px 18px 0 rgba(0, 0, 0, 0.12), inset 0 3px 5px -1px rgba(0, 0, 0, 0.4);
}

.inset-shadow-8dp {
    box-shadow: inset 0 8px 10px 1px rgba(0, 0, 0, 0.14), inset 0 3px 14px 2px rgba(0, 0, 0, 0.12), inset 0 5px 5px -3px rgba(0, 0, 0, 0.4);
}

.inset-shadow-12dp {
    box-shadow: inset 0 12px 16px 1px rgba(0, 0, 0, 0.14), inset 0 4px 22px 3px rgba(0, 0, 0, 0.12), inset 0 6px 7px -4px rgba(0, 0, 0, 0.4);
}

.inset-shadow-16dp {
    box-shadow: inset 0 16px 24px 2px rgba(0, 0, 0, 0.14), inset 0 6px 30px 5px rgba(0, 0, 0, 0.12), inset 0 8px 10px -5px rgba(0, 0, 0, 0.4);
}




.text-block {
    width: 400px;
    border: 1px solid rgba(0,0,0,0.1);
    padding: 16px;
}


.text-block1 {
    display: grid;
    color: #444;
    border: 1px solid #eee;
    max-width: 800px;
    margin: 32px;
    background: #fff;
    padding: 16px;
    border-radius: 16px;
    box-shadow: 2px 4px 12px rgba(0, 0, 0, 0.08);


    grid-template-columns: [title-start image-start text-start] auto [title-end image-end text-end];
    grid-template-rows: [image-start] auto [image-end title-start] auto [title-end text-start] auto [text-end];

    &>._title {
        grid-column: title-start / title-end;
        grid-row: title-start / title-end;

        font-size: 20px;
    }
    &>._image {
        grid-column: image-start / image-end;
        grid-row: image-start / image-end;
    }
    &>._text {
        grid-column: text-start / text-end;
        grid-row: text-start / text-end;

        font-size: 20px;
        line-height: 120%;
   }
}

