@import "layout";

.grid {
    position: relative;

    .rows {
        position: relative;
        @extend .vertical;
        @extend .layout;
        @extend .center;

        .row-title {}

        .row-text {
            width: 100%;
            max-width: 1200px;
        }

        .row-cols {
            position: relative;
            @extend .horizontal;
            @extend .layout;
            @extend .wrap;
            // @extend .self-stretch;
            // align-self: stretch;


            // width: 100%;
            // max-width: 1200px;
            margin-left: 2.5%;
            margin-right: 2.5%;

            .col-1of2 {
                position: relative;
                @extend .flex;
                min-width: 400px;
            }

            .col-1of3 {
                position: relative;
                width: 300px;
                min-width: 300px;
                border: 1px solid #eee;
                height: 200px;
                border-radius: 32px;
                background: #fff;
                box-shadow: 2px 4px 12px rgba(0, 0, 0, 0.08);
                margin: 16px;
                padding: 16px;
            }
        }
    }
}

.txt-title-big {
    font-size: 3.375rem;
    color: #001A39;
    line-height: 150%;
    margin-bottom: 2.5rem;
}

.txt-title-mid {
    font-size: 1.875rem;
    font-weight: 400;
    color: #001A39;
    line-height: 150%;
    margin-bottom: 1rem;
}

.txt-title-small {
    font-size: 1.5rem;
    font-weight: 400;
    color: #001A39;
    line-height: 150%;
}

.txt-text-big {
    font-size: 1.25rem;
    color: rgb(0,26,56);
}

.txt-text-mid {
    font-size: 1rem;
    font-weight: 400;
    color: rgb(0,26,56);
}

.txt-text-small {
    font-size: 0.875rem;
    color: #8795a1;
}